import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RecipesComponent } from './recipes/recipes.component';
import { ShoppingListComponent } from './shopping-list/shopping-list.component';
import { RecipeStartComponent } from './recipes/recipe-start/recipe-start.component';
import { RecipeDetailComponent } from './recipes/recipe-detail/recipe-detail.component';
import { RecipeEditComponent } from './recipes/recipe-edit/recipe-edit.component';
import { SigninComponent } from './auth/signin/signin.component';
import { RegisterComponent } from './auth/register/register.component';
import { RegisterconfirmationComponent } from './registerconfirmation/registerconfirmation.component';
import { AuthGuardService } from './service/auth-guard.service';


const routes: Routes = [
  { path: '',  redirectTo: '/signin',pathMatch: 'full' },
  { path: 'recipes', component: RecipesComponent,canActivate:[AuthGuardService] , children:[
    { path: '',component: RecipeStartComponent},
    { path: 'new',component: RecipeEditComponent},
    { path: ':id',component: RecipeDetailComponent},
    { path: ':id/edit',component: RecipeEditComponent}
  ] },
  { path: 'shopping-list', component: ShoppingListComponent ,canActivate:[AuthGuardService] },
  {path:'signin',component:SigninComponent},
    {path:'register',component:RegisterComponent},
    {path:'register/success',component:RegisterconfirmationComponent}
    
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
