import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Employee, HttpClientService } from 'src/app/service/httpclient.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  user: Employee = new Employee("","",);

  constructor(
    private httpClientService: HttpClientService,
    private router:Router,private route:ActivatedRoute
  ) { }

  ngOnInit() {
  }

  createEmployee(form :NgForm): void {
    const value=form.value;
    const newEmployee =new Employee(value.username,value.password);
    console.log(newEmployee);
   this.httpClientService.createEmployee(newEmployee)
        .subscribe( data => {
          alert("Employee created successfully.");
          this.router.navigate(['register/success']);
        });

  }
  onsignIn(){
    this.router.navigate(['/signin']);
  }

}
